.product {
  height: calc(100vh - 60px);
  padding-top: 60px;
  display: grid;
  place-items: center;
  background-color: whitesmoke;
}
.product-box {
  height: 80vh;
  width: 70vw;
  background-color: white;
  border: solid silver 1px;
  display: flex;
}
.product-img {
  height: 100%;
  width: max-content;
}
.product-img img {
  height: 100%;
}
.product-info {
  margin-left: 50px;
  padding: 70px 0px;
  position: relative;
}
.product-name {
  font-size: xx-large;
  font-weight: 700;
}
.product-brand {
  font-size: larger;
  color: gray;
}
.product-details {
  margin: 50px 0px 20px 0px;
}
.product-details-list {
  margin-left: 10px;
  font-size: larger;
  font-weight: 600;
}
.product-btns {
  position: absolute;
  bottom: 70px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.product-bn-btn {
  margin-bottom: 10px;
  padding: 10px;
  background-color: goldenrod;
  border: none;
  color: rgb(24, 32, 45);
  font-weight: 700;
  border-radius: 5px;
  font-size: large;
  cursor: pointer;
}
.product-atc-btn {
  padding: 10px;
  border-radius: 5px;
  font-size: large;
  border: none;
  cursor: pointer;
  background-color: whitesmoke;
}
